.card{
    width: 100%; 
    max-height: 570px;
    background-color: lightgray !important;
    color: black;
    font-size: 30px;
    /* margin: 0 20px; */
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}
.team__section {
    /* background-color: white; */
    padding: 30px 0;
    padding-left: 10px;
    padding-right: 10px;
    transition: all 0.5s ease;
}
.card-bg{
    background-color: rgb(134, 134, 134);
}

a {
    text-decoration: none;
    color: #fff;
}

.pageTitle {
    text-transform: uppercase;
    font-size: 2rem;
    letter-spacing: 3px;
    margin-right: -3px;
    margin-bottom: 5rem;
    text-align: center;
    position: relative;
}
.pageTitle:after {
    content: "";
    width: 8rem;
    height: .6rem;
    background-color: #45e19f;
    position: absolute;
    bottom: -2rem;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 2rem;
}
.card-wrapper {
    
    transition:transform 1s;
    /* box-shadow: 8px 8px 12px rgba(0,0,0, .2); */
     box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.card .card-img{
    width: 100%;
    height: 12rem;
    object-fit: cover;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 78%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 78%);
    /* border-top-right-radius: 10%; */
}

.profile-img{
    width: 10rem;
    height: 10rem;
    object-fit: cover;
    border-radius: 50%;
    margin-top: -6rem !important;
    z-index: 999;
    border: 5px solid #ebeef8;
    margin: 0 auto;
}

.card h1 {
    font-size: 1.5rem;
    color: #333;
    margin: 12px 0;
    align-items: center;
    text-align: center;
}
.job-title {
    color: #777;
    font-size: 12px;
    font-weight: 300;
    text-align: center;
}
.about {
    font-size: 12px;
    margin: 12px 0;
    font-style: italic;
    text-align: center;
    color: #333;
}
.card .btn {
    padding: 10px 20px;
    background-color: #445ae3;
    border-radius: 2rem;
    text-transform: uppercase;
    color: #eee;
    font-size: 11px;
    transition: all .5s;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    
}

.card .btn:hover{
    transform: translateY(-2px);
    box-shadow: 8px 8px 32px rgba(0,0,0, .2);
}
.card .btn:active{
    transform: translateY(0);
    box-shadow: none;
}
.social-media {
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    padding: 8px 0;
    margin-top: 16px;
    border-top: 1px solid rgba(0,0,0,.1)
}

.icons {
    transition: color .6s;
}
.icons:hover{
    color: #45e19f;
}

.card-wrapper:hover {
    transform: scale(1.03);
}
